.successful-pop-up-container {
    background: #FFFFFF;
    width: 480px;
    height: 400px;
    box-shadow: 0px 0px 12px rgba(0, 0, 0, 0.1);
    border-radius: 4px;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    align-items: center;
}

.close-favourites {
    cursor: pointer;
    display: flex;
    align-self: flex-end;
    margin-top: 32px;
    margin-right: 32px;
}

.successful-text-container {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    align-self: center;
    font-family: Heebo;
    font-style: normal;
    font-weight: normal;
    font-size: 25px;
    line-height: 35px;
    letter-spacing: 0.4px;
    color: #373738;
    margin-top: 32px;
}

.btn-successful {

}


.btn-successful {
    margin-top: 32px;
    width: 220px;
    height: 36px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 6px 16px;
    background: #FD7114;
    border-radius: 5px;
}

.btn-successful:hover {
    margin-top: 32px;
    width: 220px;
    height: 36px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 6px 16px;
    cursor: pointer;
    background: #FD7114;
    mix-blend-mode: normal;
    opacity: 0.8;
    border-radius: 5px;
}

.btn-successful span {
    font-family: Heebo;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    letter-spacing: 0.4px;
    color: #FFFFFF;
    flex: none;
    order: 0;
    align-self: stretch;
    flex-grow: 0;
    margin: 0px 10px;
}

.successful-lite {
    margin-top: 24px;
    width: 220px;
    height: 40px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 6px 16px;
    border-radius: 5px;
    min-width: 200px;
    background: #FFFFFF;
    border: 2px solid #FD7114;
}

.successful-lite span {
    font-family: Heebo;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    letter-spacing: 0.4px;
    color: #FD7114;;
    flex: none;
    order: 0;
    align-self: stretch;
    flex-grow: 0;
    margin: 0px 10px;
}

.successful-text {
    margin-top: 10px;
    width: 347px;
    font-family: Heebo;
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 22px;
    text-align: center;
    color: #373738;
}